import {React} from "react";
import {Route} from 'react-router-dom';
import Aloitus from './components/Aloitus';
import UusiResepti from './components/UusiResepti';
import Muokkaustila from './components/Muokkaustila';
import Reseptihaku from './components/Reseptihaku';
import PoistaResepti from './components/PoistaResepti';
import { useEffect, useState } from 'react';

function App() {

  const [reseptit, setReseptit] = useState([]);
  

  const lisaaResepti = () => {
    localStorage.setItem("reseptilista", JSON.stringify(reseptit));
  }

  const avaaReseptit = () => {
    if (localStorage.getItem("reseptilista")) {
      setReseptit(JSON.parse(localStorage.getItem("reseptilista")));
    } else {
      setReseptit([]);
    }
  }

  useEffect( () => {
    avaaReseptit();
  }, []);

  useEffect( () => {
    lisaaResepti();
  }, [reseptit]);


  return (
    <div>
      
      <Route path="/uusi">
        <UusiResepti
          reseptit = {reseptit}
          setReseptit = {setReseptit}
        />
      </Route>

      <Route path="/haku">
        <Reseptihaku
          reseptit = {reseptit}
        />
      </Route>

      <Route path="/muokkaus/:id">
        <Muokkaustila
          reseptit = {reseptit}
          setReseptit = {setReseptit}
        />
      </Route>

      <Route path="/poisto/:id">
        <PoistaResepti
          reseptit = {reseptit}
          setReseptit = {setReseptit}
        />
      </Route>

      <Route path="/" exact>
        <Aloitus
          reseptit = {reseptit}
          setReseptit = {setReseptit}
          avaaReseptit = {avaaReseptit}
        />
      </Route>

    </div>
  );
}

export default App;
