import { Container, Typography, MenuItem, InputLabel, Select, TextField, FormControl, Button } from "@material-ui/core";
import { useRef, useState } from "react";
import { useHistory, useParams, Link } from "react-router-dom";


function Muokkaustila (props) {

    const history = useHistory();
    const {id} = useParams();
    const lomaketiedot = useRef({});

    const muokattavaResepti = props.reseptit.filter((resepti) => {
        return (resepti.id === id);
    })[0];

    const [luokka, setLuokka] = useState(muokattavaResepti.luokka);
    
    const kasitteleLomake = (e) => {
        e.preventDefault();

        if (!lomaketiedot.current.nimi) {
            lomaketiedot.current.nimi = muokattavaResepti.nimi;
        }

        if (!lomaketiedot.current.raakaAineet) {
            lomaketiedot.current.raakaAineet = muokattavaResepti.raakaAineet;
        }

        if (!lomaketiedot.current.ohje) {
            lomaketiedot.current.ohje = muokattavaResepti.ohje;
        }

        
        let uusiReseptiApu =    {
                                    id: muokattavaResepti.id,
                                    nimi: lomaketiedot.current.nimi,
                                    luokka: luokka,
                                    raakaAineet: lomaketiedot.current.raakaAineet,
                                    ohje: lomaketiedot.current.ohje,
                                    avattu: false
                                }

        let apuReseptit = props.reseptit.filter((resepti) => {
            return (resepti.id !== id);
        });

        apuReseptit = [...apuReseptit, uusiReseptiApu];
        apuReseptit.sort(function (a,b) {
            const nimiA = a.nimi.toUpperCase();
            const nimiB = b.nimi.toUpperCase();
            if (nimiA < nimiB) {
                return -1;
            }
            if (nimiA > nimiB) {
                return 1;
            }
            return 0;
        });
        props.setReseptit(apuReseptit);
        history.push("/");
    }
        

        const lisaaLuokka = (e) => {
            setLuokka(e.target.value);
        }   

        const syoteKasittelija = (e) => {
            let arvo = (e.target.value);

            lomaketiedot.current[e.target.name] = arvo;
        }    

    return (
    
        <Container
            maxWidth = "xs"
        >
            <form onSubmit={kasitteleLomake}>

            <Typography 
                variant="h5"
                style={{
                    marginTop: "20px"
                }}
            >Muokkaa haluamasi tiedot: </Typography>

            <TextField
                label = "Reseptin nimi"
                name="nimi"
                fullWidth = {true}
                onChange={syoteKasittelija}
                style={{marginTop: "10px"}}
                defaultValue={muokattavaResepti.nimi}
            />

            <FormControl
                fullWidth={true}
                style={{marginTop: "10px"}}
            >
                <InputLabel 
                    id="luokat"
                >Reseptiluokka</InputLabel>

                <Select
                    labelId="luokat"
                    value={luokka}
                    onChange={lisaaLuokka}
                >
                    <MenuItem value={"liharuuat"}>Liharuuat</MenuItem>
                    <MenuItem value={"kasvisruuat"}>Kasvisruuat</MenuItem>
                    <MenuItem value={"kanaruuat"}>Kanaruuat</MenuItem>
                    <MenuItem value={"kalaruuat"}>Kala- ja äyriäisruuat</MenuItem>
                    <MenuItem value={"muutruuat"}>Muut ruuat</MenuItem>
                </Select>
            </FormControl>

            <TextField
                label="Raaka-aineet"
                placeholder="Kirjoita tähän raaka-aineet ja niiden määrät, yksi raaka-aine kullekin riville"
                name="raakaAineet"
                multiline
                rows={10}
                onChange={syoteKasittelija}
                fullWidth
                variant="outlined"
                style={{marginTop: "10px"}}
                defaultValue={muokattavaResepti.raakaAineet}
            />

            <TextField
                label="Ohje"
                name="ohje"
                multiline
                rows={5}
                onChange={syoteKasittelija}
                fullWidth
                variant="outlined"
                style={{marginTop: "10px"}}
                defaultValue={muokattavaResepti.ohje}
            />

            <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                style={{
                    marginTop: "15px",
                    marginBottom: "10px",
                    backgroundColor: "#628272"

                }}
            >Tallenna muokatut tiedot</Button>

            </form>

            <Button
                component={Link}
                to="/"
                variant="contained"
                color="primary"
                fullWidth
                style={{
                        marginTop: "15px",
                        marginBottom: "10px",
                        backgroundColor: "#628272"

                }}
            >Palaa alkuun</Button>

        </Container>
    
    );
}

export default Muokkaustila;