import { Container, Button, Typography } from "@material-ui/core";
import { useHistory, useParams } from "react-router-dom";
import { Link } from 'react-router-dom';

function PoistaResepti (props) {

    const history=useHistory();
    const {id} = useParams();

    const poistettavaResepti = props.reseptit.filter((resepti) => {
        return (resepti.id === id);
    })[0];

    const poistaResepti = (e) => {
        e.preventDefault();

        let apuReseptit = props.reseptit.filter((resepti) => {
            return (resepti.id !== id);
        })

        props.setReseptit([...apuReseptit]);
        history.push("/");
    }

    return (
    
        <Container
            maxWidth="xs"
        >
        
        <form onSubmit={poistaResepti}>

            <Typography
                style={{marginTop: "20px"}}
            >
                Haluatko varmasti poistaa reseptin "{poistettavaResepti.nimi}"?
            </Typography>

            <Button
                type = "submit"
                variant="contained"
                style={{
                    marginTop: "10px",
                    backgroundColor: "#21574a",
                    color: "white"
    
                }}
                fullWidth
            >Poista resepti</Button>

            <Button
                component={Link}
                to="/"
                variant="contained"
                style={{
                    marginTop: "10px",
                    backgroundColor: "#21574a",
                    color: "white"
    
                }}
                fullWidth
            >
                Peru poisto ja palaa alkuun
            </Button>

        </form>

        </Container>
    
    );
}

export default PoistaResepti;