import { Container, Typography, TextField, Button, Accordion, AccordionSummary, AccordionDetails } from "@material-ui/core";
import { ExpandMore } from "@material-ui/icons";
import { useState } from "react";
import { Link } from 'react-router-dom';


function Reseptihaku (props) {

    const [hakusana, setHakusana] = useState();
    const [haettavat, setHaettavat] = useState([]);

    const haeReseptit = (e) => {
        e.preventDefault();

        let apuHaettavat = [];

        props.reseptit.forEach(resepti => {
            if(resepti.raakaAineet.toLowerCase().includes(hakusana.toLowerCase())) {
                apuHaettavat.push(resepti);
            }
        });
        setHaettavat(apuHaettavat);
    }


    return (
        <Container
            maxWidth = "xs"
        >

        <form onSubmit={haeReseptit}>

            <Typography
                variant="h5"
                align="center"
                style={{marginTop: "20px", marginBottom: "20px"}}
            >RESEPTIHAKU</Typography>

            <Typography
                style={{marginTop: "20px", marginBottom: "20px"}}
            >
                Voit hakea reseptejä raaka-aineen mukaan. Alle tulostuvat kaikki reseptit, jotka sisältävät haetun raaka-aineen.
            </Typography>

            <TextField
                label = "Haettava raaka-aine"
                fullWidth = {true}
                onChange={(e) => {setHakusana(e.target.value);}}
                style={{marginTop: "10px"}}
            />

            <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                style={{
                    marginTop: "20px",
                    marginBottom: "10px",
                    backgroundColor: "#21574a"

                }}
            >Hae reseptejä</Button>
            <Button
                component={Link}
                to="/"
                variant="contained"
                color="primary"
                fullWidth
                style={{
                        marginTop: "15px",
                        marginBottom: "10px",
                        backgroundColor: "#628272"

                }}
            >Palaa alkuun</Button>


        </form>

        {haettavat.map ((haettava, idx) => {
            return(
                <Accordion
                    key={idx}
                >
                    <AccordionSummary
                        expandIcon={<ExpandMore/>}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography
                            variant="h6"
                        >{haettava.nimi}</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography>
                            <b>Raaka-aineet:</b> {haettava.raakaAineet.split("\n").map(str => <p>{str}</p>)}
                            <b>Ohje: </b> <p>{haettava.ohje}</p>
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            );
        })}

        </Container>
    );
}

export default Reseptihaku;