import { Button, Container, FormControl, InputLabel, MenuItem, Select, TextField } from "@material-ui/core";
import { useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import {v4 as uuid} from 'uuid';
import { Link } from 'react-router-dom';


function UusiResepti (props) {

    const lomaketiedot = useRef({});
    const [luokka, setLuokka] = useState("");
    const [virheilmoitus, setVirheilmoitus] = useState({});
    const history = useHistory();
    
    const kasitteleLomake = (e) => {
        e.preventDefault();

        let virheet = {};

        if(!lomaketiedot.current.nimi) {
            virheet={...virheet, nimi: "Kirjoita reseptin nimi"}
        }

        if(luokka === "") {
            virheet={...virheet, luokka: "Virhe"}
        }

        if(!lomaketiedot.current.raakaAineet) {
            virheet={...virheet, raakaAineet: "Kirjaa vähintään yksi raaka-aine"}
        }

        if(!lomaketiedot.current.ohje) {
            virheet={...virheet, ohje: "Kirjoita resesptin ohje"}
        }

        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({...virheet});
        }
        else {
            setVirheilmoitus({});

            let uusiReseptiApu =    {
                                        id: uuid(),
                                        nimi: lomaketiedot.current.nimi,
                                        luokka: luokka,
                                        raakaAineet: lomaketiedot.current.raakaAineet,
                                        ohje: lomaketiedot.current.ohje,
                                        avattu: false
                                    }

            let apuReseptit = [...props.reseptit, uusiReseptiApu];
            apuReseptit.sort(function (a,b) {
                const nimiA = a.nimi.toUpperCase();
                const nimiB = b.nimi.toUpperCase();
                if (nimiA < nimiB) {
                    return -1;
                }
                if (nimiA > nimiB) {
                    return 1;
                }
                return 0;
            });
            props.setReseptit(apuReseptit);
            history.push("/");
        }
    }

    const lisaaLuokka = (e) => {
        setLuokka(e.target.value);
    }   

    const syoteKasittelija = (e) => {
        let arvo = (e.target.value);

        lomaketiedot.current[e.target.name] = arvo;
    }    

    return (
    <Container
        maxWidth = "xs"
    >
        <form onSubmit={kasitteleLomake}>

            <TextField
                label = "Reseptin nimi"
                name="nimi"
                fullWidth = {true}
                error={Boolean(virheilmoitus.nimi)}
                helperText={virheilmoitus.nimi}
                onChange={syoteKasittelija}
                style={{marginTop: "10px"}}
            />

            <FormControl
                fullWidth={true}
                style={{marginTop: "10px"}}
            >
                <InputLabel 
                    id="luokat"
                    error={Boolean(virheilmoitus.luokka)}                
                >Reseptiluokka</InputLabel>

                <Select
                    labelId="luokat"
                    value={luokka}
                    onChange={lisaaLuokka}
                    
                >
                    <MenuItem value={"liharuuat"}>Liharuuat</MenuItem>
                    <MenuItem value={"kasvisruuat"}>Kasvisruuat</MenuItem>
                    <MenuItem value={"kanaruuat"}>Kanaruuat</MenuItem>
                    <MenuItem value={"kalaruuat"}>Kala- ja äyriäisruuat</MenuItem>
                    <MenuItem value={"muutruuat"}>Muut ruuat</MenuItem>
                </Select>
            </FormControl>

            <TextField
                label = "Raaka-aineet"
                placeholder="Kirjoita tähän raaka-aineet ja niiden määrät, yksi raaka-aine kullekin riville"
                name="raakaAineet"
                multiline
                rows={10}
                error={Boolean(virheilmoitus.raakaAineet)}
                helperText={virheilmoitus.raakaAineet}
                onChange={syoteKasittelija}
                fullWidth
                variant="outlined"
                style={{marginTop: "10px"}}
            />

            <TextField
                label = "Ohje"
                name="ohje"
                multiline
                rows={5}
                error={Boolean(virheilmoitus.ohje)}
                helperText={virheilmoitus.ohje}
                onChange={syoteKasittelija}
                fullWidth
                variant="outlined"
                style={{marginTop: "10px"}}
            />

            <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                style={{
                    marginTop: "15px",
                    marginBottom: "10px",
                    backgroundColor: "#628272"

                }}
            >Lisää resepti</Button>

            <Button
                component={Link}
                to="/"
                variant="contained"
                color="primary"
                fullWidth
                style={{
                        marginTop: "15px",
                        marginBottom: "10px",
                        backgroundColor: "#628272"

                }}
            >Palaa alkuun</Button>


        </form>


    </Container>
    
    )
}

export default UusiResepti;